<template>
  <div class="complaintReportingAdd">
    <div class="bg" ref="complaintReportingAdd">
      <div class="header van-nav-bar">
        <section class="headerBox1">
          <div class="headerCon">
            <div class="left" @click="onLeftClick">
              <img :src="backImg" alt srcset />
            </div>
            <div class="center">
              <div class="title">{{title}}</div>
            </div>
            <div class="left"></div>
          </div>
        </section>
      </div>
      <div class="top" :style="boxStyle2">
        <div class="header1" :style="boxStyle3"></div>

        <!-- 小区选择 -->
        <div class="areaChooseBox">
          <div class="chooseArea">
            <a
              href="javascript:void(0)"
              class="chooseAreaTitle"
              @click.stop="activationArea"
              v-text="chooseAreaTitle"
            ></a>
            <a href="javascript:void(0)" class="chooseAreaIcon" @click.stop="activationArea">
              <img :src="pullDownIcon" class="iconImg" />
            </a>
          </div>
        </div>
      </div>
      <div class="conBox" :style="boxStyle">
        <div class="conWrap">
          <!-- 投诉报修提交 -->
          <div class="conBoxTop"></div>

          <!-- 标签选择 -->
          <section class="labelBox">
            <div class="labelHeader">{{labelHeader}}</div>
            <div class="labelList">
              <div
                :class="{labelItem:true,labelChoose:activeLabel == index}"
                v-for="(item,index) in labelList"
                :key="index"
                @click="chooseLabel(index)"
                v-if="index < 6"
              >{{item.labelName}}</div>
            </div>
            <div class="space"></div>
          </section>

          <!-- 问题描述 -->
          <section class="questionBox">
            <div class="question">
              <div class="questionHeader">{{questionHeader}}</div>
              <div class="questionCon">
                <textarea
                  ref="textArea"
                  v-model="content"
                  class="questionInfo"
                  cols="30"
                  rows="5"
                  maxlength="1000"
                  placeholder="请输入详细问题，我们一定努力解决"
                ></textarea>
              </div>
            </div>
          </section>

          <section class="uploadImgBox">
            <div class="uploadImgList">
              <div
                class="uploadImgItem"
                v-for="(item,index) in uploadImgList"
                :key="index"
                v-if="index < 4 "
              >
                <img :src="item" class="uplaodImg" />
                <div class="rightImg">
                  <img :src="deleteImg" class="uplaodImg" @click="deleteUploadImg(index)" />
                </div>
              </div>
              <div class="uplaodImgBox" v-if="uploadImgList.length <= 3">
                <img :src="uploadImg" class="uplaodImg" @click="openImg" />
              </div>
            </div>
          </section>
        </div>
      </div>
    </div>

    <!-- 提交按钮 -->
    <section :style="boxStyle1" class="submitBox" v-if="isUpdate == 0 && btnFlag == 0">
      <submit-btn @save="submit"></submit-btn>
    </section>

    <section :style="boxStyle1" class="submitBox" v-if="isUpdate == 1 && btnFlag == 0">
      <submit-btn @save="update"></submit-btn>
    </section>

    <div class="speechDiv" v-show="isShowSpeechDiv">
      <div class="speechSearchDiv" v-show="isShowSpeechDiv && !isNoRecognizeResult">
        <div class="tryToSay" v-show="!isShowSpeechResult">试试这样说</div>
        <div class="speechexample" v-show="!isShowSpeechResult">
          “电梯坏了”
          <br />“公共区域垃圾堆放”
          <br />“房屋漏水”
          <br />“走廊地面湿滑”
          <br />...
        </div>
        <div class="showSpeechResult" v-show="isShowSpeechResult">{{this.recognizeResult}}</div>
        <img
          class="speechGifDiv"
          src="@/assets/images/mall/search_voice.gif"
          alt
          v-show="!isShowSpeechResult"
        />
        <img
          class="speechGifDiv"
          src="@/assets/images/mall/search_voice.png"
          alt
          v-show="isShowSpeechResult"
        />
      </div>
      <div class="noRecognizeResultDiv" v-show="isNoRecognizeResult">
        <img class="noRecognizeResultimg" src="@/assets/images/mall/no_voicesearch_img.png" alt />
        <div class="noRecognizeResultText">未检测到语音</div>
      </div>
    </div>
    <!-- <div
      class="voiceBtn"
      @touchstart="goToSpeech"
      @touchend="stopSpeech"
      ref="voiceBtnKeyBoard"
      v-show="isShowVoiceBtn"
    >
      <img
        class="voiceBtnImg"
        src="@/assets/images/mall/search_red.png"
        alt
        v-show="!isPressSpeechBtn"
      />
      <div class="voiceBtnText" v-show="!isPressSpeechBtn">按住说话</div>
      <img
        class="voiceBtnImg"
        src="@/assets/images/mall/search_white.png"
        alt
        v-show="isPressSpeechBtn"
      />
      <div class="voiceBtnTextInPress" v-show="isPressSpeechBtn">按住说话</div>
    </div> -->

    <!-- 小区选择 -->
    <van-popup v-model="areaActive" position="bottom">
      <van-picker
        show-toolbar
        title
        :columns="areaLabelList"
        :default-index="areaIndex"
        @cancel="onCancel"
        @confirm="onConfirm"
      />
    </van-popup>
    <!-- 调用图片 -->
    <!-- <van-popup 
        v-model=""
        position="bottom"
        >
         <div class="lineBottom" >
             <div class="lineBom">
                <van-button type="default"  block @click="useCamera">拍照</van-button>
             </div>
             <div class="lineBom">
                <van-button type="default" block  @click="getPicture">从手机相册选择</van-button>
             </div>
             <div class="lineBom">
               <van-button type="default"  block @click="lineCancel">取消</van-button>
             </div>
         </div>
    </van-popup>-->
    <van-action-sheet
      v-model="imgShow"
      :actions="actions"
      cancel-text="取消"
      round
      close-on-click-action
      @select="addSelect"
    />
  </div>
</template>

<script>
  import {complaintReportingAdd} from './complaintReportingAdd'
  export default {
    name: 'complaintReportingAdd',
    mixins:[complaintReportingAdd]
  }
</script>

<style scoped lang="scss">
